body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*body {background-color: #222; color: #eee; background-attachment: fixed;}*/
body {background-color: #222; color: #eee; background-image: url(https://source.unsplash.com/800x600/?programming); background-size: cover; background-attachment: fixed;}
a {color: #aaa;}
a:visited {color: #777;}

li {margin: 15px 5px; display: inline-block;}

.bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-image: -webkit-linear-gradient(100deg, transparent -100%, #222 90%);
    background-image: linear-gradient(350deg, transparent -100%, #222 90%);
}

.content {
    position: absolute;
}

@media all and (display-mode: standalone) {
    .App-bar {
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        height: 50px;
        background-color: yellow;
    }
    .App-bar::before {
        content: 'App launched as installed application';
    }
}
